import { FpjsProvider } from '@fingerprintjs/fingerprintjs-pro-react';
import { FingerprintJSData } from './FingerPrintJSData.component';
import { fpjsClientConfig } from './clientConfig';

export const FingerprintJSApp = () => {
    return (
        <FpjsProvider loadOptions={{
            apiKey: fpjsClientConfig.apiKey,
            region: fpjsClientConfig.region
        }}>
            <FingerprintJSData />
        </FpjsProvider>
    )
};