import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import { HistoryContextProvider } from "./history";
import { BrowserRouter, Route, Routes, HashRouter } from "react-router-dom";
import { ThumbmarkApp } from "./thumbmark";
import { FingerprintJSApp } from "./fingerprintjs";

const rootElement = document.getElementById("root")!;
const root = ReactDOM.createRoot(rootElement);

root.render(
  <React.StrictMode>
    <HistoryContextProvider>
      <HashRouter>
        <Routes>
          <Route path="" element={<ThumbmarkApp />} />
          <Route path="fp" element={<FingerprintJSApp />} />
          <Route path="thumbmark" element={<ThumbmarkApp />} />
          <Route path="v4" element={<App />} />
          <Route path="v3" element={<App />} />
          <Route path="v2" element={<App />} />
          <Route path="v1" element={<App />} />
        </Routes>
      </HashRouter>
    </HistoryContextProvider>
  </React.StrictMode>
);
