import { useVisitorData } from '@fingerprintjs/fingerprintjs-pro-react';

import { Container, Header } from "../layout"
import { RecordDetail } from "../record-page/record-detail.component"

export const FingerprintJSData = () => {
    const { isLoading, data } = useVisitorData({ extendedResult: true }, { immediate: true });

    const handleCopy = () => {
        window.navigator.clipboard.writeText(
            JSON.stringify(data, null, 2)
        );

        window.alert('Copied!');
    };

    return (
        <>
            <Header>
                <h4>{isLoading ? 'N/A' : data?.visitorId}</h4>
                <p><small>Hash provided by FingerprintJS Pro</small></p>
            </Header>
            <Container>
                <section>
                    <h2>Components</h2>
                    <button disabled={isLoading || !data} onClick={handleCopy}>Copy</button>
                    <RecordDetail>
                        {!isLoading ? (
                            <pre>
                                {JSON.stringify(data, null, 2)}
                            </pre>
                        ) : 'N/A'}
                    </RecordDetail>
                </section>
            </Container>
        </>
    )
}